<template>
  <div class="paste">
    <div class="container ">

      <div class="columns is-multiline">
        <div class="column is-12">
          <Aads1></Aads1>
        </div>
        <div class="column is-3">
          <Juicy1></Juicy1>
        </div>
        <div class="column is-6">
          <template v-if="paste">
            <div class="">
              <b-notification type="is-info" aria-close-label="Close notification">
                Please no report links, if not is broke
              </b-notification>
              <div class="card">
                <div class="card-header is-shadowless">
                  <h1 class="card-header-title has-text-primary is-size-4">
                    {{ paste.name }}
                  </h1>
                </div>
                <div class="card-content">
                  <div class="body">
                    {{ paste.body }}
                  </div>
                </div>
              </div>
              <br>
              <div class="">
                <h2 class="has-text-secondary is-size-5">Links</h2>
                <span class="tag is-success">Optime</span> |
                <span class="tag is-warning">Report</span> |
                <span class="tag is-danger">Broke</span>
                <hr>
                <template v-for="item in paste.links">
                  <div class="box is-radiusless" :key="item.name">
                    <div class="media">
                      <div class="media-left">
                        <b-button
                          :type="linkType(`${item.status}`)"
                          label="Obtener"
                          tag="a"
                          :href="`/link/${item.id}`"
                          target="__blank"
                        ></b-button>
                      </div>
                      <div class="media-content">
                        <h2 class="has-text-success">{{ item.name }}</h2>
                      </div>
                      <div class="media-right">


                        <template v-if="(item.status === 'on')">
                          <b-dropdown aria-role="list">
                              <button class="button is-primary" slot="trigger" slot-scope="{ active }">
                                  <b-icon :icon="active ? 'menu-up' : 'menu-down'"></b-icon>
                              </button>
                              <b-dropdown-item aria-role="listitem" @click="setReport(`${item.id}`)">Report</b-dropdown-item>
                          </b-dropdown>
                        </template>


                      </div>
                    </div>
                  </div>
                </template>
              </div>


            </div>
          </template>
          <template v-else>
            <div class="card">
              <div class="card-header is-shadowless">
                <div class="card-header-title px-5">
                  <b-skeleton></b-skeleton>
                </div>
              </div>
              <div class="card-content">
                <b-skeleton></b-skeleton>
              </div>
            </div>

            <br>
            <div class="box">
              <div class="media">
                <div class="media-left">
                  <b-skeleton width="60px" height="25px"></b-skeleton>
                </div>
                <div class="media-content">
                  <b-skeleton></b-skeleton>
                </div>
                <div class="media-right">
                  <b-skeleton width="400%"></b-skeleton>
                </div>
              </div>
            </div>
          </template>
        </div>
        <div class="column is-3">
          <Aads2></Aads2>
        </div>

        <div class="column is-12">
          <Juicy2></Juicy2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Paste from '../../api/Paste'
// import _ from 'lodash'
import Aads2 from '@/components/ads/A-ADS2.vue'
import Aads1 from '@/components/ads/A-ADS1.vue'
import Juicy1 from '@/components/ads/Juicy1.vue'
import Juicy2 from '@/components/ads/Juicy2.vue'

export default {
  name: 'WebPaste',
  components: {
    Aads1, Aads2, Juicy1, Juicy2
  },
  data () {
    return {
      paste: {}
    }
  },
  methods: {
    getPastes: function () {
      Paste.getWebPaste(this.$route.params.id).then((response) => {
        if(response.status === 200) {
          this.paste = response.data
        }
      }).catch((errors) => {
        if(errors.response.status === 404) {
          this.$router.push({ name: 404 })
        }
      })
    },

    linkType: function (status) {
      var linkStatus = 'is-success'
      if(status === 'report') {
        linkStatus = 'is-warning'
      }

      if(status === 'broke') {
        linkStatus = 'is-danger'
      }

      return linkStatus
    },
    setReport: function (id) {
      Paste.linkReport(id).then((response) => {
        if(response.status === 200){
          this.$buefy.notification.open({
            type: 'is-warning',
            message: 'Submited Report!'
          })
        }
      }).catch((errors) => {
        console.log(errors)
      }).then(() => {
        this.getPastes()

      })
    }
  },
  mounted () {
    this.getPastes()
  }
}
</script>
